<template>
  <v-container>
    <v-dialog v-model="show_detail" fullscreen hide-overlay>
      <v-card>
        <v-toolbar dark color="primary">
          <v-btn icon dark @click="show_detail = false">
            <v-icon>mdi-close</v-icon>
          </v-btn>
          <v-toolbar-title>Settings</v-toolbar-title>
        </v-toolbar>
        <v-card-text>
          <p style="white-space: pre-line; margin-bottom: 5px;" v-for="(log, index) in logs" :key="`log-${index}`">{{log.detail}}</p>
        </v-card-text>
      </v-card>
    </v-dialog>
    <v-row>
      <v-col>
        <v-simple-table>
          <template v-slot:default>
            <thead>
            <tr>
              <th class="text-left">Environment</th>
              <th class="text-right" style="width: 180px;">Created At</th>
              <th class="text-right" style="width: 180px;">Updated At</th>
              <th class="text-right" style="width: 100px;">Status</th>
              <th class="text-right" style="width: 100px;"></th>
            </tr>
            </thead>
            <tbody>
            <tr v-for="item in items" :key="item.name">
              <td>{{ item.environment_name }}</td>
              <td style="text-align: right;">{{ formatDate(item.created_at) }}</td>
              <td style="text-align: right;">{{ formatDate(item.updated_at) }}</td>
              <td style="text-align: right;">
                <span v-if="item.current_status === 0">Waiting</span>
                <span v-if="item.current_status === 1">Completed</span>
                <span v-if="item.current_status === 2">Failed</span>
              </td>
              <td style="text-align: right">
                <v-btn x-small @click="showDetails(item.id)">Details</v-btn>
              </td>
            </tr>
            <tr v-if="items.length === 0">
              <td colspan="3">No record found</td>
            </tr>
            </tbody>
          </template>
          <v-overlay :value="loading">Loading...</v-overlay>
        </v-simple-table>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>

import axios from 'axios';
import moment from 'moment';

export default {
  data() {
    return {
      loading: false,
      items: [],
      show_detail: false,
      logs: []
    }
  },
  async mounted() {
    await this.loadItems();
  },
  methods: {
    async loadItems() {
      this.loading = true;
      try {
        let response = await axios.get('/api/deployments',{params: {project_id: this.$store.state.project.selected.id}});
        this.items = response.data.deployments;
      } catch (e) {
        console.log(e);
      } finally {
        this.loading = false;
      }
    },
    async showDetails(id) {
      this.loading = true;
      try {
        let response = await axios.get('/api/deployments/' + id);
        this.logs = response.data.logs;
        this.show_detail = true;
      } catch (e) {
        console.log(e);
      } finally {
        this.loading = false;
      }
    },
    itemStatus(item) {
      return item.current_status === 7 ? 'Completed' : 'Waiting';
    },
    formatDate(date) {
      return moment(date).format('DD MMM YYYY HH:mm:ss');
    }
  }
}
</script>
